import React from 'react';

const FullStackDevBlog = () => (
    <>
        <h3>Intro</h3>
        <p>
            Full stack developer is a term we hear all over the place. It became one of the most desirable positions 
            for developers in the industry. In this blog I will describe my personal view of this idea of full-stack development,
            as well as some general concepts and technologies behind it.
        </p>
        <p>           
            <i>Note: This blog will not go into coding details.</i>
        </p>
        <h3>What is Full Stack Development?</h3>
        <p>
            When we refer to full-stack development, the usual context is programming in the web environment, and specifically developing
            the whole parts of a web application by a single developer or a group of developers without a specific separation
            between different parts of the application.
        </p>
        <p>    
            To develop a full stack web application we are required to use several technologies. 
            These technologies are usually quite different one from the other and require different
            skills and knowledge. A developer that can handle all of these technologies technically and conceptualy 
            is considered as a full-stack developer.
        </p>
        <p>
            Imagine that a single builder builds the whole house - the walls, the electricity, the water,
            the plumbing, the floors, the paint, well, you get the picture. It is not likely, for one person to
            handle all of it. In full-stack development, however, this is exactly what is expected.
        </p>
        <p>
            The image below demonstrated the 3 main parts of a full stack application - front-end AKA the
            client side, back-end AKA the server side and the database.
        </p>
        <img 
            alt='full-stack scheme' 
            src='data:image/png;base64,'
            height='300'
            width='300'
        />;
        <p>
            There is also a fourth domain which is becoming more and more popular these days - publishing our 
            application, specifically when using the cloud services, which requires yet some more knowledge 
            and expertise that is quite different from the skills required for the application development.
        </p>
        <p>
            So, what do you need to become a full stack developer? Which languages, frameworks, tools and databases?
            The following sections will try to answer that question.
        </p>
        <h3>Front-End Technologies</h3>
        <p>
            The front-end part of the web application is often called <u>the client</u>, to represent the one part of the application
            that is accessible to the user (client) for performing actions and getting information. Front-end development takes care of
            the user interface (UI), including the appearance, the styling, the user experience (UX), the responsiveness of 
            the UI and of course the content. 
        </p>
        <p>
            It is not possible to be a serious front-end developer without having a solid knowledge in these three technologies:
            <ul>
                <li>
                <a href='https://www.w3schools.com/html/' target='blank'><b>HTML</b></a> which is responsible for the content of the website.
                </li>
                <li>
                <a href='https://www.w3schools.com/css/' target='blank'><b>CSS / SCSS</b></a> which is responsible for the stying and the appearance
                of the website. 
                </li>
                <li>
                <a href='https://www.w3schools.com/js/' target='blank'><b>JavaScript </b></a> which is the engine behind and provides the responsiveness
                and the flexibility of the website.
                </li>
            </ul>
            However, it doesn't end here. To develop a modern website, it is very recommended to have a solid knowledge in at least one of the three
            most famous JavaScript based frameworks: 
            <ul>
                <li><a href='https://reactjs.org/' target='blank'><b>ReactJS</b></a></li>
                <li><a href='https://angular.io/' target='blank'><b>Angular </b></a></li>
                <li><a href='https://vuejs.org/' target='blank'><b>Vue</b></a></li>
            </ul>
            There are others, but these three are the most popular and recommended. 
        </p>
        <p>
            Knowing all of the above is good, and it will allow you to start doing some nice things in the front-end
            world. But there is more. A bigger and more complicated application will require to know some more concepts and
            technologies.
            <ul>
                <li>State management like <a href='https://redux.js.org/' target='blank'><b>Redux</b></a></li>
                <li>Testing frameworks like <a href='https://jestjs.io/' target='blank'><b>jest</b></a> and <li><a href='https://github.com/airbnb/enzyme' target='blank'><b>enzyme</b></a></li></li>
                <li>Mock server to imitate the server without using an actual one. Read more about mock servers <a href='http://www.mock-server.com/' target='blank'>here</a></li>
            </ul>
        </p>
        <p>
            While HTML and CSS are rather straight forward and easy to learn and understand, JavaScript is different. It is a
            rich, advanced and fascinating programming language to learn with a huge community of users and information over the internet. 
            Investing time in learning vanilla JavaScript, therfore, is advised, before moving on to the fancy front-end frameworks and
            the complementary technologies as stated in this section.
        </p>
        <h3>Back-End Technologies</h3>
        <p>
            If front-end development is the "face" of the application, back-end development is its "brains" and "internal organs".
            It gets orders from the user, generates responses, makes queries to the database, performs calculating algorithms,
            scrapes web pages and many more. Basically, all the business logic of the application is handled in the back-end.
            Back-end development is also known as server-side development, as it develops the part of the application that
            serves clients' requests.
        </p>
        <p>
            Front-end development requirements are somehow specific, as you can't get around the technologies mentioned
            in the previous part. Back-end development is much more diverse. 
            Many programming languages have the web server capabilities, so there are
            a lot of choices. Here are some of the most popular ones:
            <ul>
                <li>
                <a href='https://www.java.com/en/' target='blank'><b>Java</b></a>
                </li>
                <li>
                <a href='https://www.python.org/' target='blank'><b>Python </b></a>
                </li>
                <li>
                <a href='https://rubyonrails.org/' target='blank'><b>Ruby on Rails</b></a>
                </li>
                <li>
                <a href='https://www.php.net/' target='blank'><b>PHP</b></a>
                </li>
                <li>
                <a href='https://nodejs.org/en/' target='blank'><b>NodeJS</b></a>
                </li>
            </ul>
            So, what to choose? Well, it depends on different things.
        </p>
        <p>
            <u>Application:</u> there are programming languages, that are commonly used for certain things. 
            For example, if the main domain of the application is in the Machine Learning, Deep Learning and Artificial Intelligence fields, 
            then it would make a lot of sense to use Python as your main server language. 
        </p>
        <p>    
            <u>Prior knowledge:</u> if the application domain doesn't require specific libraries, the logical
            choice for the server language would be just a language that the programmers are comfortable with.
        </p>
        <p>
            <u>Single technology:</u> to make things simpler for the full-stack developers, one of the most popular choices for the server
            side is NodeJS. It is based on JavaScript, it is open source with a great community of developers behind it, and it requires to master
            only a single programming language and its frameworks - JavaScript (again??).
        </p>
        <p>
            Server side programming organizes the whole app and connects between its different parts. A typical back-end flow would
            be to listen to the client's requests, generate some action (like approach the database or trigger some action like a calculation algorithms
            or web scraping) and send a response back to the client.
        </p>
        <h3>API</h3>
        <p>One of the most important and typical tasks in web development is determining the interfaces between the back-end and the front-end sides.
           When a web app is developed by a single developer (i.e. full stack developer), this task becomes easier, however it is still extremely important
           to define and try not to change along the way, unless it is mandatory.
        </p>
        <p>
           The most commonly and widely used API is called <a href='https://en.wikipedia.org/wiki/Representational_state_transfer' target='blank'><b>REST API</b></a>.
           This API is used to pass data between back-end and front-end. For example, if a user triggers an event (like a button click)
           it can be used to generate an API request from the front-end to the back-end. The back-end will process the request
           based on the data that it got, and will issue a response, that will be sent back to the front-end code and usually generate some
           change in the UI. 
           The requests and the responses are generated in a <a href='https://www.json.org/' target='blank'><b>JSON</b></a> format which are very easy to
           handle in JavaScript and are very readable and easy to understand.
        </p>
        <p>
            Another common way to pass parameters from front-end to back-end is by using query parameters, also known as URL 
            parameters. These values are passed within the URL format with specific keys and can be easily handled
            on the server side.
        </p>
        <p>
            Proper definition of the API allows the developer to work independantly on the front-end and the back-end side
            and minimize contact switching. It is especially correct when using a mock server to imitate the real server, what
            makes the integration between the front-end and the back-end much simpler.
        </p>
        <h3>Databases</h3>
        <p>
            Every modern and dynamic website, will eventually require using a database. This part is often overlooked by developers 
            who tend and prefer to focus on programming and programming languages but failing to properly define and handle 
            the database part of the app, could cause it to become unreliable and unusable.
        </p>
        <p>
            Examples to the usages of databases are:
            <ul>
                <li>
                    Keeping the user’s personal data (like bank account) safely
                </li>
                <li>
                    Persistency of users' actions in the web app, like commenting, liking, quoting, ...
                </li>
                <li>
                    Storing personal images and fetching them on demand
                </li>
                <li>
                    Video on demand services of the likes of Netflix, Hulo and others.
                </li>
            </ul> 
        </p>
        <p>
            There are two main types of databases: relational (SQL) and non-relational (NoSQL).
            There are many good databases in each of the types. The most common for the SQL type are MySQL, Oracle and Postgres
            and for the NoSQL type are MongoDB, Redis and others.
        </p>
        <p>
            SQL based databases are basically a collection of tables with schemas, that represent the attributes and the data
            types within the table. You can read, write, update and delete the data.
            These databases are well maintained, documented and are very stable.
        </p>
        <p>
            NoSQL based databases have started to gain popularity with the increased complexity of the web applications.
            The more the data is complexed, the harder it is to have it structured and organized, and thus, the usage of NoSQL
            databases is more convenient for that matter.
        </p>
        <p>
            There are many sources over the internet that can help us to pick the right one, but in general, the selection of the database
            should be based on the type of data that we expect to have. Will it be well structured, like bank accounts, then
            using the stable and safe SQL databases would be advised.
            For IoT apps or large analytics, which would require complexed data structures, it would probably be better 
            to use NoSQL.
        </p>
        <h3>Summary</h3>
        <p>
            Full-stack development is fascinating. The amount of challenges is huge. The technology is constantly changing and improving,
            and you must be on the top of your game all the time. You need to understand and work in different environments, and invest a lot
            of time in design and planning. The reward of the effort in the end is seeing your app running on the World Wide Web.
            For those who are interested to try it, here are a couple of links for courses to get started.
        </p>
        <h3>Links</h3>
        <ul>
            <li>
            <a href='https://www.linkedin.com/learning/react-creating-and-hosting-a-full-stack-site/react-for-full-stack-solutions' target='blank'><b>LinkedIn Learning Course 1</b></a>
            </li>
            <li>
            <a href='https://www.linkedin.com/learning/learning-full-stack-javascript-development-mongodb-node-and-react' target='blank'><b>LinkedIn Learning Course 2</b></a>
            </li>
        </ul> 
    </>
);


export default FullStackDevBlog;